/* eslint-env browser */



(function() {
  'use strict';

  // Check to make sure service workers are supported in the current browser,
  // and that the current page is accessed from a secure origin. Using a
  // service worker from an insecure origin will trigger JS console errors. See
  // http://www.chromium.org/Home/chromium-security/prefer-secure-origins-for-powerful-new-features
  var isLocalhost = Boolean(window.location.hostname === 'localhost' ||
      // [::1] is the IPv6 localhost address.
      window.location.hostname === '[::1]' ||
      // 127.0.0.1/8 is considered localhost for IPv4.
      window.location.hostname.match(
        /^127(?:\.(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)){3}$/
      )
    );

  if ('serviceWorker' in navigator &&
      (window.location.protocol === 'https:' || isLocalhost)) {
    navigator.serviceWorker.register('service-worker.js')
    .then(function(registration) {
      // updatefound is fired if service-worker.js changes.
      registration.onupdatefound = function() {
        // updatefound is also fired the very first time the SW is installed,
        // and there's no need to prompt for a reload at that point.
        // So check here to see if the page is already controlled,
        // i.e. whether there's an existing service worker.
        if (navigator.serviceWorker.controller) {
          // The updatefound event implies that registration.installing is set:
          // https://slightlyoff.github.io/ServiceWorker/spec/service_worker/index.html#service-worker-container-updatefound-event
          var installingWorker = registration.installing;

          installingWorker.onstatechange = function() {
            switch (installingWorker.state) {
              case 'installed':
                // At this point, the old content will have been purged and the
                // fresh content will have been added to the cache.
                // It's the perfect time to display a "New content is
                // available; please refresh." message in the page's interface.
                break;

              case 'redundant':
                throw new Error('The installing ' +
                                'service worker became redundant.');

              default:
                // Ignore
            }
          };
        }
      };
    }).catch(function(e) {
      console.error('Error during service worker registration:', e);
    });
  }

  // Your custom JavaScript goes here

  //region HELPER
  window.onTextBoxChange = function onTextBoxChange(element) {
    const characters = findCharacters(element);

    var tokens = skey_lexer(characters[2]);

    if (tokens.length > 0) {
      tokens = skey_eval(tokens);
      // console.log("tokens", tokens);
      replaceCharacters(element, tokens, characters[0], characters[1]);
    }
  }

  const SKIP = " \n\t";

  function findCharacters(element) {

    const end = element.selectionStart;
    var text = element.value;
    // console.log('text', text);

    var start = 0;

    for(var i = end; i >= 0; i--) {
      var t = text[i];
      if (SKIP.indexOf(t) !== -1) {
        start=i + 1;
        break;
      }
    }

    // console.log("WORD (" + start + ", " + end + ")" + text.substr(start, end-start));

    return [start, end, text.substr(start, end-start)];
  }

  function replaceCharacters(element, tokens, start, end) {
    const text = tokens.map(function (token) {return token[0];}).join("");
    // console.log(" - Final text to be replaced", text);

    element.value = element.value.substring(0, start) + text + element.value.substring(end);
  }
  //endregion


  //region S-key engine
  const ONE_KEY_ACCENTED_LETTER = 'ONE_KEY_ACCENTED_LETTER';
  const ACCENT_KEY = 'ACCENT_KEY';
  const VOWEL = 'VOWEL';
  const CONSONANT = 'CONSONANT';

  const token_exprs = [
    [/[ \n\t]+/, null],
    [/[1-5]+/, ACCENT_KEY],
    [/[d6-9\[\]]/, ONE_KEY_ACCENTED_LETTER],
    [/[^AEIOUĂẮẰẲẴẶÂẦẨẪẬÊẾỀỂỄỆÍÌỈĨỊƠỚỜỞỠỢƯỨỪỬỮỰ6-9]+/i, CONSONANT],
    [/[AEIOUĂẮẰẲẴẶÂẦẨẪẬÊẾỀỂỄỆÍÌỈĨỊƠỚỜỞỠỢƯỨỪỬỮỰ]+/i, VOWEL]
  ];

  const VOWEL_PREFERENCES = "ÊẾỀỂỄỆƠỚỜỞỠỢ";

  const ACCENT_DICT = {
    "a1": "á",
    "a2": "à",
    "a3": "ả",
    "a4": "ã",
    "a5": "ạ",

    "ă1": "ắ",
    "ă2": "ằ",
    "ă3": "ẳ",
    "ă4": "ẵ",
    "ă5": "ặ",

    "â1": "ấ",
    "â2": "ầ",
    "â3": "ẩ",
    "â4": "ẫ",
    "â5": "ậ",

    "e1": "é",
    "e2": "è",
    "e3": "ẻ",
    "e4": "ẽ",
    "e5": "ẹ",

    "ê1": "ế",
    "ê2": "ề",
    "ê3": "ể",
    "ê4": "ễ",
    "ê5": "ệ",

    "i1": "í",
    "i2": "ì",
    "i3": "ỉ",
    "i4": "ĩ",
    "i5": "ị",

    "o1": "ó",
    "o2": "ò",
    "o3": "ỏ",
    "o4": "õ",
    "o5": "ọ",

    "ơ1": "ớ",
    "ơ2": "ờ",
    "ơ3": "ở",
    "ơ4": "ỡ",
    "ơ5": "ợ",

    "u1": "ú",
    "u2": "ù",
    "u3": "ủ",
    "u4": "ũ",
    "u5": "ụ",

    "ư1": "ứ",
    "ư2": "ừ",
    "ư3": "ử",
    "ư4": "ữ",
    "ư5": "ự"
  };

  var ONE_KEY_ACCENT_DICT = {
    "6": "â",
    "7": "ê",
    "8": "ô",
    "9": "ă",
    "[": "ư",
    "]": "ơ",
    "d": "đ"
  };

  function skey_lexer(characters) {
    var pos = 0;
    var tokens = [];

    while(pos < characters.length) {
      var match = null;
      for(var i = 0; i < token_exprs.length; i++) {
        const token_expr = token_exprs[i];
        const pattern = token_expr[0];
        const tag = token_expr[1];
        const text = characters[pos];

        // console.log("pattern", pattern);

        match = pattern.test(text);

        if (match) {
          if (tag) {
            const token = [text, tag];
            tokens.push(token);
          }
          break;
        }
      }

      if (!match) {
        // console.log("error")
      }
      else {
        pos = pos + 1;
      }
    }

    return tokens;

  }

  function skey_eval(tokens) {
    // console.log("START OF skey_eval()");

    tokens = processOneKeyAccentedLetter(tokens);
    // console.log('after one key accented letter: ', tokens);

    tokens = accentize(tokens);
    // console.log("token after first accentize", tokens);

    return tokens;
  }

  function processOneKeyAccentedLetter(tokens) {
    for(var i = 0; i < tokens.length; i++) {
      const token = tokens[i];
      if (token[1] === ONE_KEY_ACCENTED_LETTER) {
        token[0] = ONE_KEY_ACCENT_DICT[token[0]];
        token[1] = VOWEL;
        break;
      }
    }
    return tokens;
  }

  function accentize(tokens) {
    var firstVowel, secondVowel, thirdVowel, lastConsonant, accent, highPriorityVowel;
    for(var i = 0; i < tokens.length; i++) {
      const token = tokens[i];

      // console.log(" - Considering token: ", token);

      if (token[1] === VOWEL) {
        if (VOWEL_PREFERENCES.indexOf(token[0].toUpperCase()) !== -1) {
          // console.log(" - Found a higher priority vowel", token);
          highPriorityVowel = [i, token];
        }
        thirdVowel = secondVowel ? [i, token] : thirdVowel;
        secondVowel = firstVowel ? [i, token] : secondVowel;
        firstVowel = firstVowel ? firstVowel : [i, token];
      }
      else if (token[1] === CONSONANT) {
        lastConsonant = (firstVowel || secondVowel || thirdVowel) ? [i, token] : lastConsonant;
      }
      else if (token[1] === ACCENT_KEY) {
        accent = [i, token];
      }

      // console.log(" - first vowel", firstVowel);
      // console.log(" - second vowel", secondVowel);
      // console.log(" - third vowel", thirdVowel);
      // console.log(" - last consonant", lastConsonant);
      // console.log(" - accent", accent);
      if (highPriorityVowel && accent) {
        if (finallizeAccentCharacters(highPriorityVowel[1], accent[1])) {
          tokens.splice(accent[0], 1);
        }
      }
      else if (thirdVowel && accent) {
        if (finallizeAccentCharacters(secondVowel[1], accent[1])) {
          tokens.splice(accent[0], 1);
        }
      }
      else if (secondVowel && lastConsonant && accent) {
        if (finallizeAccentCharacters(secondVowel[1], accent[1])) {
          tokens.splice(accent[0], 1);
        }
      }
      else if (secondVowel && accent) {
        if (finallizeAccentCharacters(firstVowel[1], accent[1])) {
          tokens.splice(accent[0], 1);
        }
      }

      else if (firstVowel && accent) {
        if (finallizeAccentCharacters(firstVowel[1], accent[1])) {
          tokens.splice(accent[0], 1);
        }
      }
    }

    return tokens;

  }


  function finallizeAccentCharacters(vowel, accent) {
    const found = ACCENT_DICT[vowel[0] + accent[0]];

    if (found) {
      vowel[0] = found;
      return true;
    }
    return false;
  }
  //endregion

})();
